.header {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: calc(100vh - 120px);
    width: 100%;
    background-image: url(/static/media/bg.cec0478a.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.header-navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 80px;
    background-color: rgba(255, 99, 71, 0.562);
}

.navbar-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    font-size: 24px;
    font-weight: bold;
    grid-gap: 20px;
    color: rgba(255, 255, 255, 0.767);
    text-shadow: 4px 2px rgba(0, 0, 0, 0.192);
}

.menu-items {
    width: 220px;
    height: 40px;
    text-align: center;
    letter-spacing: 10px;
    border: 1px solid white;
    background-color: rgba(128, 128, 128, 0.452);
}

.menu-items:hover {
    transition: 0.2s ease-in;
    cursor: pointer;
    border: none;
    color: goldenrod;
}
.menu-items:active{
    transition: 0.1s ease-in;
    color: white;
    background-color: rgba(128, 128, 128, 0.7);
}
.footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 2%;
    background-color: rgba(255, 99, 71, 0.97);
    height: 120px;
}

.footer-copyright {
    color: white;
}

.footer-sosial p {
    display: flex;
    align-items: center;
    flex-direction: row;
    color: honeydew;
}

.footer-sosial p:hover {
    transition: 0.2 ease-in;
    cursor: pointer;
    opacity: 0.7;
    color: black;
}

.social-item {
    height: 32px;
    width: 32px;
}
.about {
    position: fixed;
    background-color: black;
    top: 0;
    left: 0;
    right: 0;
    min-height: calc(100vh - 120px);
    color: white;
}

.about-header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    height: 80px;
    color: black;
}

.about-close {
    position: fixed;
    top: 0;
    right: 0;
    color: white;
    font-weight: bold;
    margin-right: 10px;
    margin-top: 10px;
    width: 25px;
    height: 25px;
    text-align: center;
    cursor: pointer;
    /*border: 1px solid black;*/
    background-color: black;
    border-radius: 100%;
}

.about-close:hover {
    transition: .3s ease-in-out;
    color: black;
    border: 1px solid black;
    background-color: white;
}

.about-content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - (120px + 80px));
    background-color: rgba(0, 0, 255, 0.096);
    padding: 0 2%;
    flex-wrap: wrap;
    overflow: auto;
    grid-gap: 50px 10px;
}

.about-content p {
    text-align: center;
    width: 240px;
}
.blog {
    position: fixed;
    background-color: black;
    top: 0;
    left: 0;
    right: 0;
    min-height: calc(100vh - 120px);
    color: white;
}

.blog-header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    height: 80px;
    color: black;
}

.blog-close {
    position: fixed;
    top: 0;
    right: 0;
    color: white;
    font-weight: bold;
    margin-right: 10px;
    margin-top: 10px;
    width: 25px;
    height: 25px;
    text-align: center;
    cursor: pointer;
    /*border: 1px solid black;*/
    background-color: black;
    border-radius: 100%;
}

.blog-close:hover {
    transition: .3s ease-in-out;
    color: black;
    border: 1px solid black;
    background-color: white;
}

.blog-content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - (120px + 80px));
    background-color: rgba(0, 0, 255, 0.096);
    padding: 0 2%;
    flex-wrap: wrap;
    overflow: auto;
    grid-gap: 50px 10px;
}

.bolg-content-items {
    display: block;
    width: 280px;
    height: 340px;
    border: 1px solid white;
}

.bolg-content-items h4 {
    text-align: center;
}

.bolg-content-items img {
    padding: 0 30px;
    width: 100%;

}

